import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
import componentItem from '@/common/model/components/index';
import goods from '@/common/model/goods/index';
import bottomMenus from '@/common/model/bottom-menus';
import tuiScrollTop from "@/components/scroll-top/scroll-top"

export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiLoadmore,
		tuiNomore,
		componentItem,
		goods,
		bottomMenus,
		tuiScrollTop,
	},
	data() {
		return {
			ipage:1,
			current: 0,
			scrollTop: 0,
			hotSearch: [
				"休闲零食",
				"自热火锅",
				"小冰箱迷你"
			],
			components:[],
			items:[],
			query_goods_url:'',
			page:{
				ipage:0,
				hasGoods:1,
			},
			loadding: false,
			pullUpOn: true
		}
	},
	onLoad() {
		this.query();
	},
	onReachBottom(){
		this.queryItems();
	},
	methods: {
		tabbarSwitch: function(e) {
			let index = e.currentTarget.dataset.index;
			this.current = index;
			if (index != 0) {
				if (index == 1) {
					this.classify();
				} else {
					this.tui.toast("功能开发中~")
				}
			}
		},
		detail: function() {
			uni.navigateTo({
				url: '../productDetail/productDetail'
			})
		},
		classify: function() {
			uni.navigateTo({
				url: '/pages/category/category'
			})

		},
		more: function(e) {
			let key = e.currentTarget.dataset.key || "";
			uni.navigateTo({
				url: '/pages/search/search?q=' + key
			})

		},
		search: function() {
			uni.navigateTo({
				url: '/pages/search/search'
			})
		},
		queryItems() {
			console.log('url:'+this.query_goods_url);
			if(!this.query_goods_url||this.page.hasGoods==0){
				return;
			}
			this.page.ipage = this.page.ipage+1;
			this.$http.post(this.query_goods_url, {ipage:this.page.ipage}).then(res => {
				if(res.data.items&&res.data.items){
					this.items.push(
					...res.data.items
					);
				}else{
					this.page.hasGoods = 0;
				}
				this.loadding = false
			}).catch(err => {});
		},
		query() {
			this.$http.post('/app/page/home', {page_id:'youdanhui'}).then(res => {
				if(res.data.components&&res.data.components){
					this.components.push(
					...res.data.components
					);
				}
				if(res.data.items&&res.data.items.url){
					this.query_goods_url = res.data.items.url;
					this.queryItems();
				}
				uni.stopPullDownRefresh()
			}).catch(err => {});
		},
	},
	onPullDownRefresh: function() {
		this.ipage = 1;
		this.pullUpOn = true;
		this.loadding = false
	},
	onReachBottom: function() {
		if (!this.pullUpOn) return;
		this.loadding = true;
		this.queryItems();
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop
	}
}
